import { useState, useEffect } from 'react';
import '../style/Game.css';

function Game (){

    const [word, setWord] = useState(null);
    const [answer, setAnswer] = useState();
    const [isCorrect, setIsCorrect] = useState(false);


    useEffect(()=>{
        handleNext();
    }, 
    // eslint-disable-next-line
    []);

    function fetchWord(){
        fetch('https://random-words-api.vercel.app/word').then((res)=> res.json()).then(
            (result)=>{
                        let word = result[0].word.normalize('NFD').replace(/\p{Diacritic}/gu,"").toLowerCase();
                        let answer= [...word].map(
                            (char, index)=>{
                                return({
                                value: (index===0)?char:null,
                                isPreset: index===0,
                                isCorrect: index===0,
                                })
                            }
                        );

                        setWord({
                            word: word,
                            definition: result[0].definition,
                            pronunciation: result[0].pronunciation,
                        });

                        setAnswer(answer);

                        setIsCorrect(false);

                    }
        )
    }

    function handleChange(event){
        let ans = answer;
        const letterId = parseInt(event.target.id.slice(-1))
        ans[letterId].value = event.target.value.slice(-1);
        setAnswer(ans);
        checkCorrect();
        if(event.target.value.slice(-1).toLowerCase() === word.word[parseInt(event.target.id.slice(-1))]){
            let next = document.getElementById(`letter${letterId+1}`);
            if(next) next.focus();
        }
    }


    function checkCorrect(){
        let isCorrect = true;
        let ans = answer.map(
            (letter, index) => {
                if(letter.value?.toLowerCase() === word.word[index]){
                    letter.isCorrect = true;
                    return(letter);
                }else{
                    isCorrect = false;
                    return(letter);
                }
            }
        )
        setAnswer(ans);
        setIsCorrect(isCorrect);
    }

    function handleNext(){
        setWord(null);
        fetchWord();
        document.getElementById('letter1')?.focus();
    }

    return(
        <div id="game_board">
            {(word)?
            <>
                <div id="word">
                    {answer?.map(
                        (letter, index) => {
                            return(
                                <input 
                                    key={index} 
                                    id={`letter${index}`} 
                                    value={(letter.value)?letter.value:""} 
                                    readOnly={letter.isCorrect || letter.isPreset} 
                                    onChange={handleChange} 
                                    className={letter.isPreset?"preset":letter.isCorrect?"correct":"not_correct"}
                                    autoComplete="off"
                                />
                            )
                        }
                    )}
                </div>

                <div id="meaning">
                    <p id="label">Meaning</p>
                    <p>{word.definition}</p>
                </div>

                <button className="button" onClick={handleNext}>{isCorrect?'Next':'Skip'}</button>
                
                <div id="won">
                    {(isCorrect)?
                        <>
                            <p id="congrats">Congratulations ðŸŽ‰</p>
                            <p id="pronaunaciation">It's pronounced /{word.pronunciation}/</p>
                        </>
                        :<></>
                    }
                </div>

            </>
    :
            <div className="loading">
                <div></div>
                <div></div>
                <div></div>
            </div>}
        </div>
        );

}

export default Game;
