import '../style/Intro.css';

function Intro(props){

    const state = props.state;

    function start(){
        state.setStarted(true);
    }

    return(
        <div className="intro">
            <h1>Welcome to WordSpell</h1>
            <p>wordspell is a simple spelling game <br />
            your only clues are the first letter and the meaning of the word.</p>
            <p>Have fun!</p>
            <button className='button start' onClick={start}>Start</button>
        </div>
    );
}

export default Intro;