import Game from "./Game";
import Navbar from './Navbar';
import Intro from './Intro';
import { useState } from 'react';

function App() {

  const [dark, setDark] = useState(false);
  const [started, setStarted] = useState(false);

  const state = {
    dark,
    setDark,
    started,
    setStarted
  }

  return (
    <>
      <Navbar state={state} />
      {started? <Game /> : <Intro state={state} />}
    </>
  );
}

export default App;
